import $ from 'jquery';
global.jQuery = $;
require('jquery.scrollto');

import hammerjs from 'hammerjs';

import {getCurrentQueryStringParams} from '../../../../deluxe_common/static/deluxe_common/js/URLUtil';

const videojs = global.videojs;

const ACTIVE = 'active';

const CAROUSEL_PERIOD = 5*1000;

const getCarouselPeriod = () => {
    const qsp = getCurrentQueryStringParams();
    return qsp && qsp.carousel_period ? qsp.carousel_period : 5*1000;
}

const findAndPlayVideoCarousel = (container) => {
  $(container).find('.video-js').each((i,e) => {
    const player = videojs(e);
    player.paused() ? player.play() : player.pause();
    player.toggleClass('hide');
  });
};

const currentActiveItem = (items) => {
    const index = items.findIndex((item) => {return $(item).hasClass(ACTIVE)});
    return index;
};

const changeDownArrowColor = () => {
    $(`.carousel.large`).each((i,carousel) => {
        $(carousel).find(`.carousel-homepage.${ACTIVE}`).each((i,item) => {

            const $arrow = $(carousel).find('.down-arrow');
            /* it's either the prev() on production or the prev().prev() when in draft mode */
            const $allPrevSiblings = $(carousel).parent().prevAll();
            let $anchorMenu = $([]);
            if($allPrevSiblings.length > 0 && $($allPrevSiblings[0]).hasClass('anchor-menu')){
                $anchorMenu = $($allPrevSiblings.get(0));
            } else if($allPrevSiblings.length > 1 && $($allPrevSiblings[1]).hasClass('anchor-menu')) {
                $anchorMenu = $($allPrevSiblings.get(1));
            }

            const addRemove = ($e, add, remove) => {
                $e.addClass(add);
                $e.removeClass(remove);
            };

            if($(item).hasClass('light')) {
                addRemove($arrow, 'light', 'dark');
                addRemove($anchorMenu, 'light', 'dark');
            } else {
                addRemove($arrow, 'dark', 'light');
                addRemove($anchorMenu, 'dark', 'light');
            }
        });
    });
};

const clickDot = (event) => {
    const targetNumber = event.target.dataset.slide;
    const dots = $(event.target).parent().find('li');
    const current = Array.from(dots).findIndex((li) => $(li).hasClass(ACTIVE));
    if(current != targetNumber) {
        const items = $(event.target).closest('.carousel').find('.carousel-homepage').toArray();
        switchItem(items, dots, current,targetNumber);
    }
};

const swipeLeft = (event) => {
    const items = $(event.target).closest('.carousel.large').find('.carousel-homepage').toArray();
    if(items.length == 0) { return; };
    //find the one that is currently visible
    const index = currentActiveItem(items);
    //if the item isn't found then this will make the `0` element visible
    let next = index + 1;
    //need to make sure the next one isn't larger than the list size
    if(next >= items.length) {
        next = 0;
    }
    const dots = $(event.target).closest('.carousel.large').find('.carousel-nav > ol > li');
    switchItem(items, dots, index,next);
};

const swipeRight = (event) => {
    const items = $(event.target).closest('.carousel.large').find('.carousel-homepage').toArray();
    if(items.length == 0) { return; };
    //find the one that is currently visible
    const index = currentActiveItem(items);

    let previous = index -1;
    //need to make sure the next one isn't larger than the list size
    if(previous < 0) {
        previous = items.length - 1;
    }

    const dots = $(event.target).closest('.carousel.large').find('.carousel-nav > ol > li');
    switchItem(items, dots, index,previous);
};

const switchItem = (items, dots, current, next) => {
    if(current != next) {
        $(dots[current]).removeClass(ACTIVE);
        $(dots[next]).addClass(ACTIVE);
        $(items[current]).removeClass(ACTIVE);
        $(items[next]).addClass(ACTIVE);

        const currentSlide = $(items[current])[0];
        const nextSlide = $(items[next])[0];

        findAndPlayVideoCarousel($(currentSlide));
        findAndPlayVideoCarousel($(nextSlide));
        changeDownArrowColor();
    };
};

const downArrowClick = (event) => {
    event.preventDefault();
    const sibling = $(event.target).closest('.carousel.large').next();
    $(window).scrollTo(sibling, '750', {'interrupt' : false});
}


class Carousel {

    constructor(element) {
        const that = this;
        const items = $(element).find('.carousel-homepage').toArray();
        if(items.length == 0) { throw "no items in the carousel";}

        const dots = $(element).find('.carousel-nav li');
        if(dots.length == 0) { throw "no dot navigation found for carousel";}

        items.forEach((item, index) => {
            if(index == 0) {
                $(item).addClass(ACTIVE);
                findAndPlayVideoCarousel($(item));
            } else {
                $(item).removeClass(ACTIVE);
            }
        });
        //need to add the dots for navigation
        dots.each((i,dot) => {
            $(dot).click((event) => {
                that.rotate = false;
                clickDot(event)
            });
        });

        hammerjs(element).on("swipeleft", (event) => {
          that.rotate = false;
          swipeLeft(event);
        });

        hammerjs(element).on("swiperight", (event) => {
          that.rotate = false;
          swipeRight(event);
        });

        $(element).find("a.down-arrow").each((i,e) => {
            $(e).click(downArrowClick);
        });

        that.rotate = true;
        changeDownArrowColor(items);
    }

    should_rotate() {
        return this.rotate;
    };

}

export class Carousels {
    constructor() {

        $(".carousel.large").toArray().forEach((element, index) => {
           const carousel = new Carousel(element);
           const carouselItems = $(element).find('.carousel-homepage');
           if(carouselItems.length > 1) {
                setInterval((element, carousel) => {
                    const event = { 'target': carouselItems.first().get()};
                    if(carousel.should_rotate()) {
                        swipeLeft(event);
                    }
                }, getCarouselPeriod(), element, carousel);
           }
        });
    }
}
