import $ from 'jquery';

import Cookies from 'js-cookie';

import {getGeoIP} from '../../../../cookie_banner/static/cookie_banner/js/GeoIPAPI';

const COOKIE_BANNER_CONTAINER_SELECTOR = '.cookie-banner-container';
const HIDDEN = 'hidden';
const COOKIE_NAME = 'cookie_banner'


const track = () => {
    const ga_id = global.google_analytics_id;

    if(ga_id) {
        (function(i,s,o,g,r,a,m){i['GoogleAnalyticsObject']=r;i[r]=i[r]||function(){
        (i[r].q=i[r].q||[]).push(arguments)},i[r].l=1*new Date();a=s.createElement(o),
        m=s.getElementsByTagName(o)[0];a.async=1;a.src=g;m.parentNode.insertBefore(a,m)
        })(window,document,'script','https://www.google-analytics.com/analytics.js','ga');

        ga('create', ga_id, 'auto');
        ga('send', 'pageview');
    }

    if(global.piAId && global.piCId) {
        (function() {
        function async_load(){
        var s = document.createElement('script'); s.type = 'text/javascript';
        s.src = ('https:' == document.location.protocol ? 'https://pi' : 'http://cdn') + '.pardot.com/pd.js';
        var c = document.getElementsByTagName('script')[0]; c.parentNode.insertBefore(s, c);
        }
        if(window.attachEvent) { window.attachEvent('onload', async_load); }
        else { window.addEventListener('load', async_load, false); }
        })();
    }

};

const setCookieBannerCookie = (acceptedOrNotEUBool) => {
    Cookies.set(COOKIE_NAME, {acceptedOrNotEU: acceptedOrNotEUBool});
};

const cookiePolicyAccepted = (event) => {
    //need to set a cookie so
    const parent = $(event.target).parents(COOKIE_BANNER_CONTAINER_SELECTOR);
    $(parent).addClass(HIDDEN);
    setCookieBannerCookie(true);
    track();
};

/* this function returns a promise. */
const hasCookiePolicyBeenPreviouslyAcceptedOrNoneEU = () => {
    const cookie = Cookies.getJSON(COOKIE_NAME);

    //three states - not set, set and true, set and false
    let promise = null;

    if(!cookie || cookie.acceptedOrNotEU === 'undefined' || cookie.acceptedOrNotEU === null) {

        promise = getGeoIP().then((response) => {

            let is_not_eu = false;
            if(response.country_code && response.continent_code != 'EU') {
              is_not_eu = true;
            }
            setCookieBannerCookie(is_not_eu);
            return is_not_eu;

        });
    } else {
        promise = Promise.resolve(cookie && cookie.acceptedOrNotEU);
    }
    return promise;

}

const showCookieBanner = (container) => {
    $(container).removeClass(HIDDEN);
    $(container).find('button').on('click', cookiePolicyAccepted);
};

export class CookieBanner {

    constructor() {

        $(COOKIE_BANNER_CONTAINER_SELECTOR).each((i,e) => {
            const promise = hasCookiePolicyBeenPreviouslyAcceptedOrNoneEU();

            promise.then((result) => {
                result ? track() : showCookieBanner(e);
            }, (err) => {
                console.error(err);
                showCookieBanner(e);
            });
        });

    }

}