import {fetchGet, pick} from '../../../../deluxe_common/static/deluxe_common/js/BaseAPIUtil';

export function getGeoIP() {
    /* will need to grab this url from hardcoded value in the page or swap it on publish. */
    const url = Array.from(document.querySelectorAll('[data-geo_ip_url]')).map((i) => i.dataset.geo_ip_url).pop();
    return url ? fetchGet(url) : Promise.reject('geo_ip_url not set');
}
